<template>
  <div>
    <div v-for='(string, index) in guitarInit' :key='index'>
      <span>第{{index+1}}弦</span>
      <select :value='string' @change='changeString' v-model='newStringInit[index]'>
        <option v-for="item in selectScale"
          :value="item[0]"
          :key="item[0]"
        >
        {{item[1]}}
        </option>
      </select>
    </div>
  </div>
</template>

<script>
export default {
  props: ['selectScale', 'guitarInit'],
  data(){
    return{
      newStringInit: [...this.guitarInit]
    }
  },
  methods:{
    changeString(){
      this.$emit('change-string', this.newStringInit)
    }
  },
}
</script>